<script>
	import { getContext } from 'svelte';

	const { data, xGet, yGet, yRange, xScale } = getContext('LayerCake');

	$: columnWidth = d => {
		const vals = $xGet(d);
		return Math.max(0, (vals[1] - vals[0]));
	};

	$: columnHeight = d => {
		return $yRange[0] - $yGet(d);
	};

	/* --------------------------------------------
	 * Default styles
	 */
	export let layout;
	export let stroke = '';
	export let strokeWidth = 0;
	export let dxTick = 0;
	export let dyTick = 0;
	export let formatTick = d => d;
	export let textAnchor = 'middle';

</script>

<style>
	.column-group {
		font-size: .8em;
	}
</style>

<g class="column-group">
	{#each $data as d, i}
		<rect
			class='group-rect'
			data-id="{i}"
			x="{$xScale.bandwidth ? $xGet(d) : $xGet(d)[0]}"
			y="{$yGet(d)}"
			width="{$xScale.bandwidth ? $xScale.bandwidth() : columnWidth(d)}"
			height="{columnHeight(d)}"
			fill={layout.fill}
			{stroke}
			stroke-width="{strokeWidth}"
		/>
	  <text
	  	x="{$xScale.bandwidth ? $xGet(d) + ($xScale.bandwidth() / 2) : $xGet(d)[0]}"
	  	y='{$yGet(d) - 4}'
  		dx='{dxTick}'
  		dy='{dyTick}'
  		text-anchor='{textAnchor}'>{formatTick(d.value).toFixed(layout.decimals)}{layout.unit}</text>
	{/each}
</g>